@mixin h1($color: $white) {
  display: inline-block;
  color: $color;
  font-size: 4rem;
  font-weight: 600;
  font-family: $mainFont;
  text-align: center;
  line-height: 1.1;
  letter-spacing: 7.2px;
  margin-bottom: 1.8rem;
  @media ($notebook) {
    font-size: 7.2rem;
    margin-bottom: 1.6rem; } }

@mixin h2($color: $secondaryColor) {
  color: $color;
  text-align: center;
  text-transform: uppercase;
  font-family: $mainFont;
  font-size: 5.5rem;
  font-weight: 900;
  line-height: normal;
  letter-spacing: 10px;
  @media ($phone) {
    font-size: 10rem; } }

@mixin h3($color: $gray) {
  color: $color;
  font-size: 1.7rem;
  font-weight: 300;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: 7.2px;
  margin-top: -6.1rem;
  @media ($phone) {
    font-size: 3.6rem;
    margin-top: -8.9rem; } }


@mixin h4($color: $gray) {
  color: $color;
  font-size: 3.2rem;
  font-weight: 500;
  text-transform: capitalize;
  line-height: 0.94; }

@mixin h5($color: $white) {
  color: $color;
  font-size: 2.4rem;
  font-weight: 500;
  text-align: center;
  text-transform: inherit;
  line-height: 1.67;
  margin-bottom: 1rem; }

@mixin h6($color: $white) {
  color: $color;
  font-size: 2.2rem;
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: -0.28px; }

@mixin h7($color: $white) {
  color: $color;
  font-size: 1.8rem;
  font-weight: 500;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: -0.22px; }
