.double-slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 26rem;
  @media ($notebook) {
    justify-content: space-between;
    width: 100%; }
  &__btn {
    @extend %btn;
    @extend %btn-circle_big;
    margin-right: 1rem;
    &:nth-last-child(1) {
      margin-right: 0; }
    @media ($notebook) {
      margin-right: 0; } }
  &__nav {
    @extend %navSlider; }
  &__images {
    // @extend .slide-left
    display: none;
    order: -1;
    margin-bottom: 2rem;
    @media ($notebook) {
      order: 0;
      margin-bottom: 0; } }
  &__img {
    @include img();
    @extend .slide-left;
    width: 26rem;
    height: 27rem;
    object-fit: cover;
    object-position: center;
    @media ($phone) {
      width: auto; }
    @media ($notebook) {
      width: 31.7rem; } }
  &__link {
    margin-right: 3.4rem;
    &:nth-last-child(1) {
      margin-right: 0; } } }
