.recipe {
  width: 26rem;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 6px;
  background-color: $black2;
  background-position: center;
  background-size: cover;
  padding: 3rem;
  padding-bottom: 6rem;
  @media ($phone) {
    width: 51.5rem; }
  @media ($tablet) {
    width: 54rem; }
  &__btn-back {
    @extend %btn;
    @extend %btn-circle_small;
    @extend %btn-forFlyer;
    margin-left: 20rem;
    @media ($phone) {
      margin-left: 44rem; } }
  &__nav {
    @extend %navFlyer; }
  &__alert {
    margin-bottom: 6.5rem; }
  &__disc {
    width: 28rem;
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    line-height: 1.67;
    letter-spacing: 0.7px;
    color: $white;
    margin-top: 2.5rem;
    @media ($phone) {
      width: 36rem; } }
  &__name {
    font-size: 1.4rem;
    line-height: 2.14;
    color: $white;
    margin: 4.5rem 0 6rem; }
  &__footer {
    display: flex; }
  &__views {
    color: $white;
    font-size: 2.2rem;
    vertical-align: middle; }
  &__comments {
    color: $white;
    font-size: 2.2rem;
    vertical-align: sub; } }

.bc-gray {
  background-color: $black2; }

.recipes-mini {
  @extend %btn;
  text-align: left;
  background-color: $black2;
  color: $white;
  box-shadow: 0 2px 5px 0 $boxShadow;
  border-radius: 0.7rem;
  padding: 3rem 2.5rem 2.5rem 3rem;
  margin-top: 3rem;
  &:nth-child(1) {
    margin-top: 0; }
  &_photo {
    background-image: url(../img/recipes1.jpg);
    background-position: center;
    background-size: cover;
    &:hover {
      background-image: none; } }
  &__disc {
    @include lead3();
    text-align: center;
    @media ($phone) {
      text-align: left; } }
  &__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    @media ($phone) {
      flex-direction: row; } }
  &__views {
    font-size: 2.2rem;
    vertical-align: middle; }
  &__comments {
    font-size: 2.2rem;
    vertical-align: sub; }
  &:hover {
    background-color: $primaryColor;
    .recipes-mini__disc {
      color: $black2; }
    .recipes-mini__views, .recipes-mini__comments, .info-table-counter, .info-table-data {
      color: $black3; }
    .alert {
      background-color: $black; } } }
