.callboard {
  text-align: center;
  text-decoration: none;
  margin-bottom: 3rem;
  border-bottom: 1px solid transparent;
  padding-bottom: 2rem;
  &:nth-last-child(1) {
    margin-bottom: 0; }
  &:hover {
    border-color: $opacity1; }
  @media ($phone) {
    margin-bottom: 0; }
  &__icon {
    font-size: 2rem;
    color: $opacity1; }
  &__data {
    font-size: 1.6rem;
    line-height: 1.88;
    color: $white;
    margin-top: 1.6rem; }
  &__disc {
    font-size: 1.6rem;
    line-height: 2;
    color: $opacity1; } }
