.socials {
  margin-top: 8rem;
  &__items {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 6.3rem auto 0;
    @media ($phone) {
      margin: 8.3rem auto 0; }
    @media ($notebook) {
      flex-direction: row;
      justify-content: space-between;
      align-items: stretch;
      margin: 11.3rem auto 0; } } }
