.shef {
  @extend %btn;
  max-width: 26rem;
  margin-top: 6rem;
  &:nth-child(1) {
    margin-top: 0; }
  &:hover {
    .shef__title, .shef__disc {
      text-decoration: underline; } }
  @media ($tablet) {
    &:nth-child(-n+2) {
      margin-top: 0; } }
  @media ($notebook) {
    margin-top: 0; }
  &__profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media ($tablet) {
      flex-direction: row; } }
  &__avatar {
    @include img();
    border-radius: 50%;
    margin-bottom: 1rem;
    @media ($tablet) {
      margin-bottom: 0; } }
  &__txt {
    padding-left: 0;
    text-align: center;
    @media ($tablet) {
      padding-left: 2.4rem;
      text-align: left; } }
  &__title {
    @include h7();
    text-align: left; }
  &__disc {
    @include subTxt3();
    margin-top: 0.5rem; }
  &__dishes {
    display: flex;
    justify-content: center;
    margin-top: 2.4rem;
    @media ($tablet) {
      justify-content: flex-start; } }
  &__img {
    margin-right: 1rem;
    &:last-child {
      margin-right: 0; }
    @media ($phone) {
      margin-right: 3rem;
      &:last-child {
        margin-right: 0; } } } }
