@import "./utils/index.sass";
@import "./components/index.sass";
@import "./layout/index.sass";

* {
  box-sizing: border-box; }

html,
body {
  font-family: $mainFont;
  font-size: 10px;
  font-weight: normal;
  background-color: $primaryBackground; }

.container {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 3rem;
  @media ($projector) {
    padding: 0; } }

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  &__txt {
    color: $primaryColor;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.7px;
    margin: 0.8rem 0 0 1rem; } }

.activeHeader {
  color: $primaryColor; }

.activeSlide {
  display: block; }

.activeRecipes {
  display: flex; }
