.dish {
  max-width: 30rem;
  border-radius: 6px;
  box-shadow: $boxShadow;
  background-color: $secondaryBackgroung;
  margin-top: 3rem;
  &:nth-child(1) {
    margin-top: 0; }
  @media ($phone) {
    max-width: 350px; }
  @media ($tablet) {
    &:nth-child(-n+2) {
      margin-top: 0; } }
  @media ($projector) {
    &:nth-child(-n+3) {
      margin-top: 0; } }
  &__img {
    @include img();
    width: 100%;
    height: 26rem;
    border-radius: 0.6rem 0.6rem 0 0;
    padding: 2rem 0 0 24.6rem;
    @media ($phone) {
      padding: 2.4rem 0 0 28.6rem; } }
  &__link {
    @extend %btn;
    @extend %btn-circle_small;
    @extend %btn-forFlyer; }
  &__icon {
    @extend %moveToPageDish; }
  &__txt {
    padding: 3rem 2.4rem 0 3rem; }
  &__title {
    @include h4(); }
  &__subtext {
    @include subTxt3();
    margin-bottom: 2.1rem; }
  &__disc {
    @include p1();
    width: 28rem; }
  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2.4rem 0; }
  &__rating {
    &:hover {
      .dish__rating-star {
        color: $gold; } } }
  &__rating-star {
    @include ratingStar();
    margin-right: 0.5rem;
    &:last-child {
      margin-right: 0; } }
  &__counter {
    color: $gray6;
    font-size: 1.2rem;
    margin: 0 1.3rem 0 0.5rem;
    @media ($phone) {
      margin-right: 4.3rem; } }
  &__btn {
    @extend %btn;
    @extend %btn-small; } }
