.tutorial {
  @extend %btn;
  flex-basis: calc(25% - 2.3rem);
  background-color: $secondaryBackgroung;
  text-align: left;
  padding: 2.5rem;
  border-radius: 0.6rem;
  box-shadow: $boxShadow;
  margin-top: 3rem;
  &:nth-child(1) {
    margin-top: 0; }
  @media ($phone) {
    &:nth-child(-n+2) {
      margin-top: 0; } }
  @media ($tablet) {
    flex-basis: calc(50% - 2.3rem); }
  @media ($notebook) {
    flex-basis: calc(33% - 1.5rem);
    margin-right: 2.6rem;
    &:nth-child(-n+3) {
      margin-top: 0; }
    &:nth-child(3n) {
      margin-right: 0; } }
  @media ($projector) {
    flex-basis: calc(25% - 2.3rem);
    margin-right: 0;
    &:nth-child(-n+4) {
      margin-top: 0; } }
  &__title {
    @include h6();
    margin-bottom: 2rem; }
  &__disc {
    @include p1();
    margin-top: 2.5rem;
    width: 19.3rem; }
  &:hover {
    background-color: $primaryColor;
    .tutorial__title {
      color: $black3; }
    .tutorial__disc {
      color: $black3; }
    .decor-line {
      background-color: $black3; } } }
