.about {
  background-color: $secondaryBackgroung;
  box-shadow: $boxShadow;
  padding-top: 3rem;
  padding-bottom: 5rem;
  margin-top: -36.5rem;
  overflow: hidden;
  @media ($phone) {
    margin-top: -23.5rem;
    padding-top: 7rem;
    padding-bottom: 10.7rem; }
  &__title {
    @include h2(); }
  &__txt {
    @include h3(); }
  &__main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5.2rem;
    padding: 0 6.5rem; }
  &__disc {
    @include lead3();
    text-align: center;
    width: 23.4rem;
    padding-bottom: 2rem;
    @media ($phone) {
      width: 46.4rem; }
    @media ($notebook) {
      width: 73.4rem;
      padding: 3rem 0 9rem; } } }
