.recipes {
  margin-top: 8rem;
  &__items {
    display: flex;
    flex-direction: column;
    margin: 6.3rem auto 0;
    @media ($phone) {
      margin: 8.3rem auto 0; }
    @media ($projector) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin: 11.3rem auto 0; } }
  &__slider {
    margin: 0 auto;
    @media ($projector) {
      margin: 0; } }
  &__block {
    width: 26rem;
    margin: 3rem auto 0;
    @media ($phone) {
      width: 51rem; }
    @media ($tablet) {
      width: 54rem; }
    @media ($projector) {
      margin: 0; } } }
