.social-card {
  width: 30rem;
  border-radius: 6px;
  box-shadow: $boxShadow;
  background-color: $black2;
  margin-top: 2.5rem;
  &:nth-child(1) {
    margin-top: 0; }
  @media ($notebook) {
    margin-top: 0; }
  @media ($projector) {
    width: 35rem; }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2.5rem;
    border-bottom: 2px solid $secondaryColor; }
  &__icon {
    display: flex;
    align-items: center; }
  &__upLink {
    @extend %btn;
    @extend %btn-circle_small;
    @extend %btn-social; }
  &__nav {
    @extend %navSocial; }
  &__name {
    font-size: 14px;
    text-transform: capitalize;
    line-height: 2.14;
    color: $gray7;
    margin-left: 2rem; }
  &__btn {
    padding: 0 1.8rem;
    @extend %btn;
    @extend %btn-small; }
  &__main {
    padding: 2rem 1.5rem 2rem 3rem; }
  &__main-pd0 {
    position: relative;
    padding: 0; }
  &__insta {
    display: none; }
  &__btn-insta {
    @extend %btn;
    @extend %btn-circle_small;
    @extend %btn-forInstagram;
    position: absolute;
    top: calc(25% - 2rem);
    right: calc(50% - 2rem); }
  &__navFlyer {
    @extend %navFlyer; }
  &__img {
    @include img();
    width: 33.33%;
    @media ($projector) {
      width: auto; } }
  &__date {
    font-size: 14px;
    line-height: 2.14;
    color: $gray5; }
  &__disc {
    width: 22.5rem;
    height: 12rem;
    font-size: 14px;
    line-height: 2.14;
    color: $opacity2;
    overflow-y: auto;
    scroll-behavior: smooth;
    @media ($projector) {
      width: 28rem; } }
  &__disc-h16 {
    height: 15.8rem; }
  &__link {
    color: $primaryColor;
    font-size: 14px;
    line-height: 2.14;
    margin-top: 1rem; } }
