// Decor
@mixin decorLine($backgroundColor: $primaryColor) {
  background-color: $backgroundColor;
  width: 6rem;
  height: 0.2rem; }

@mixin titleTine {
  margin: 0.8rem auto 0; }

@mixin ratingStar($color: $gray6) {
  color: $color;
  font-size: 1.9rem; }

// Icons
%navSlider {
  font-size: 1.6rem;
  color: $gray4; }

%moveToPageDish {
  font-size: 1.4rem;
  color: $white; }

%navFlyer {
  font-size: 1.3rem;
  color: $white; }

%navSocial {
  font-size: 1.7rem;
  color: $gray5; }

// Decor
.decor-line {
  @include decorLine(); }

.title-line {
  @include titleTine(); }
