@mixin lead($color: $white) {
  color: $color;
  opacity: 0.7;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.63;
  letter-spacing: -0.2px; }

@mixin lead2($color: $gray2) {
  color: $color;
  font-size: 1.6rem;
  // font-weight: 300
  line-height: 1.63; }

@mixin lead3($color: $white) {
  color: $color;
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.88;
  @media ($phone) {
    font-size: 1.6rem; } }

@mixin p1($color: $opacity1) {
  color: $color;
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.38; }

@mixin p2($color: $opacity2) {
  color: $color;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 2.14; }

@mixin sub_txt($color: $white) {
  color: $color;
  opacity: 0.5;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 4.29; }

@mixin sub_txt2($color: $white) {
  color: $color;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 2.14; }

@mixin subTxt3($color: $gray3) {
  color: $color;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.33;
  letter-spacing: -0.15px; }

@mixin alertTitle($color: $white) {
  color: $color;
  font-size: 10px;
  font-weight: 900;
  font-stretch: condensed;
  text-transform: uppercase;
  line-height: 2.5;
  letter-spacing: 1.25px; }
