header {
  width: 100%;
  // height: 108rem
  height: auto;
  max-width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 0.8rem;
  margin: 0 auto;
  overflow: hidden;
  @media ($tablet) {
    height: 108rem; } }

.divider {
  margin: 0;
  border: none;
  border-bottom: 2px solid $opacity0; }

.header {
  display: none;
  @media ($phone) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3.4rem 0; } }

.navbar {
  &__items {
    display: flex; }
  &__item {
    margin-right: 4rem;
    &:nth-last-child(1) {
      margin-right: 0; } }
  &__link {
    color: $white;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 1.4rem;
    &:hover {
      color: $primaryColor;
      text-decoration: underline; } } }

.branding {
  display: flex;
  flex-direction: column;
  align-items:  center;
  padding: 8rem 0 40rem;
  &__slogan {
    @include h1();
    width: 31rem;
    text-transform: capitalize;
    @media ($phone) {
      width: 70rem; }
    @media ($notebook) {
      width: 90rem; } }
  &__slogan-span {
    font-weight: 200; }
  &__disc {
    @include lead();
    width: 30rem;
    text-align: center;
    @media ($phone) {
      width: 55rem; }
    @media ($tablet) {
      width: 60rem; } }
  &__btn {
    @extend %btn;
    @extend %btn-big;
    margin-top: 4rem; } }
