.shefs {
  margin-top: 8rem;
  &__items {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 8.3rem auto 0;
    @media ($phone) {
      align-items: center; }
    @media ($tablet) {
      flex-direction: row;
      justify-content: space-around;
      margin: 11.3rem auto 0; }
    @media ($notebook) {
      justify-content: space-between; } } }
