%btn {
  display: inline-block;
  border: none;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  user-select: none;
  &:focus {
    outline: none; } }

%btn-big {
  background-color: $primaryColor;
  border: none;
  border-radius: 4.3rem;
  color: $black1;
  text-transform: capitalize;
  font-size: 1.8rem;
  font-weight: bold;
  letter-spacing: 3.6px;
  padding: 2rem 4.9rem;
  &:hover {
    background-color: darken($primaryColor, 10%);
    color: $white; } }

%btn-small {
  background-color: $btnColorBackground;
  border-radius: 3rem;
  border: solid 2px $borderColor;
  color: $opacity1;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 900;
  line-height: 4;
  letter-spacing: 2px;
  padding: 0 2.9rem;
  &:hover {
    background-color: $primaryColor;
    border-color: $primaryColor;
    color: $black2; } }

%btn-circle_big {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  border: solid 2px $borderColor;
  background-color: $btnColorBackground;
  &:hover {
    background-color: $primaryColor;
    border-color: $primaryColor;
    .double-slider__nav {
      color: $black1; } } }

%btn-circle_small {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%; }

%btn-forFlyer {
  background-color: $opacity3;
  &:hover {
    background-color: $primaryColor; } }

%btn-forInstagram {
  background-color: $primaryColor;
  &:hover {
    background-color: darken($primaryColor, 15%);
    .navFlyer {
      color: $white; } }
  .navFlyer {
    color: $gray4; } }

%btn-social {
  border: solid 2px $borderColor;
  background-color: $btnColorBackground;
  &:hover {
    background-color: $primaryColor;
    border-color: $primaryColor;
    .navSocial {
      color: $white; } } }
